.Auth {
  margin: 100px auto;
  padding: 20px;
  width: 80%;
  text-align: center;
  box-shadow: 0 2px 3px #ccc;
  border: 2px solid #eee;
  box-sizing: border-box;
}

.ErrorMessage {
  color: red;
  font-weight: bold;
}

@media (min-width: 600px) {
  .Auth {
    width: 500px;
  }
}
